
body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
  color: #34495e;
  background-color: #edeef0;
}
.svg__home,
.svg__article {
  width: 20px;
  height: 20px;
}
.svg__code {
  width: 22px;
  height: 20px;
}
.l-mH--auto {
  margin: 0 auto;
}
.l-ta--c {
  text-align: center;
}
.l-flexH--sa {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.l-flexV--c {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.l-page--Full {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.l-pt10 {
  padding-top: 10px;
}
.l-pl10 {
  padding-left: 10px;
}
.u-link {
  text-decoration: none;
  color: currentColor;
}
.u-link--Active {
  color: #1abc9c;
}
.u-ul--Reset {
  list-style-type: none;
  padding-left: 0;
}
.u-borderBox {
  box-sizing: border-box;
}
.u-clearfix:after,
.u-clearfix:before {
  display: table;
  content: " ";
}
.u-clearfix:after {
  clear: both;
}
.u-boxShadow {
  box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.3);
}
.u-zIndex10 {
  z-index: 10;
}
.u-zIndex50 {
  z-index: 50;
}
button {
  border: none;
  outline: none;
  box-shadow: none;
  background: none;
  color: currentColor;
  padding: 0;
  cursor: pointer;
}
.b-menu {
  width: 25px;
  height: 25px;
  position: relative;
}
.b-menu:before,
.b-menu:after {
  content: ' ';
  position: absolute;
  background-color: #fff;
  display: block;
  width: 100%;
  height: 3px;
  left: 0;
  top: 11px;
  border-radius: 3px;
  transition: all 0.5s cubic-bezier(0.66, 0.1, 0.38, 1.45);
  transform-origin: 50% 50%;
}
.b-menu:before {
  transform: translateY(-6px);
}
.b-menu:after {
  box-shadow: 0 6px 0 0 #fff;
}
.b-menu--Active:before {
  transform: rotateZ(45deg) translateY(0);
}
.b-menu--Active:after {
  box-shadow: 0 0 0 0 #fff;
  transform: rotateZ(135deg) translateY(0);
}
.md-content img {
  display: block;
  max-width: 100%;
  margin: 20px 0;
}
.md-content a {
  color: #1abc9c;
}
.md-content h1,
.md-content h2,
.md-content h3,
.md-content h4,
.md-content h5,
.md-content h6 {
  padding-bottom: 0.3em;
  border-bottom: 1px solid #dcdcdc;
}
.md-content p {
  line-height: 1.4em;
  text-indent: 1em;
}
.md-content pre {
  color: #ccdce1;
  background-color: #212e35;
  overflow: auto;
  border: 1px solid #465d65;
  padding: 25px 10px 15px;
  border-radius: 5px;
  position: relative;
}
.md-content code:before {
  position: absolute;
  top: 5px;
  left: 1em;
  content: attr(class);
  display: inline-block;
  color: #4a626b;
  font-size: 12px;
}
.md-content .hljs-function {
  color: #a87fc6;
}
.md-content .hljs-built_in {
  color: #dcb360;
}
.md-content .hljs-keyword {
  color: #eb4f6a;
}
.md-content .hljs-attr {
  color: #5477bf;
}
.md-content .hljs-literal {
  color: #cd775f;
}
.md-content .hljs-params {
  color: #da7c61;
}
.md-content .hljs-number {
  color: #60eab7;
}
.md-content .hljs-string {
  color: #a6ce83;
}
.md-content .hljs-title {
  color: #6d94db;
}
.md-content .hljs-comment {
  color: #4a626b;
}
.md-content .hljs-selector-id {
  color: #a87fc6;
}
.md-content .hljs-selector-class {
  color: #dcb360;
}
.md-content .hljs-selector-tag {
  color: #da7c61;
}
.md-content .hljs-attribute {
  color: #5477bf;
}
.md-content .hljs-meta {
  color: #eb4f6a;
}
.md-content .hljs-tag {
  color: #84d5ed;
}
.md-content .hljs-name {
  color: #e46b70;
}
.app__menu {
  background-color: #1abc9c;
}
.app__content {
  overflow: auto;
  transition: transform 0.5s;
  box-shadow: 0 0 100px 5px rgba(0,0,0,0.3);
  background-color: #edeef0;
  transform-origin: 200px center;
}
.app__content--Active {
  transform: scale(0.9) translateX(200px);
}
.app__bodyer {
  max-width: 1000px;
  background-color: #edeef0;
}
@media screen and (max-width: 1000px) {
.app__bodyer {
    padding: 0 10px;
}
}


